import { TableCell, TableRow } from '@mui/material';
import Link from 'next/link';
import { format } from 'date-fns';
import React from 'react';
import { getUrlOsmId } from '../../services/helpers';
import { ConvertedRouteDifficultyBadge } from '../FeaturePanel/Climbing/ConvertedRouteDifficultyBadge';
import { DEFAULT_DATA_FORMAT } from '../../config.mjs';
import { useMapStateContext } from '../utils/MapStateContext';
import { getDifficulties } from '../../services/tagging/climbing/routeGrade';
import { TickStyleBadge } from '../../services/my-ticks/TickStyleBadge';
import { FetchedClimbingTick } from '../../services/my-ticks/getMyTicks';
import { TickMoreButton } from '../FeaturePanel/Climbing/TickMoreButton';
export const MyTicksRow = ({
  fetchedTick
}: {
  fetchedTick: FetchedClimbingTick;
}) => {
  const routeDifficulties = getDifficulties(fetchedTick.tags);
  const {
    view
  } = useMapStateContext();
  const {
    name,
    style,
    date,
    apiId
  } = fetchedTick;
  return <TableRow data-sentry-element="TableRow" data-sentry-component="MyTicksRow" data-sentry-source-file="MyTicksRow.tsx">
      <TableCell data-sentry-element="TableCell" data-sentry-source-file="MyTicksRow.tsx">
        <Link href={`/${getUrlOsmId(apiId)}#${view.join('/')}`} data-sentry-element="Link" data-sentry-source-file="MyTicksRow.tsx">{name}</Link>
      </TableCell>
      <TableCell data-sentry-element="TableCell" data-sentry-source-file="MyTicksRow.tsx">
        <ConvertedRouteDifficultyBadge routeDifficulties={routeDifficulties} data-sentry-element="ConvertedRouteDifficultyBadge" data-sentry-source-file="MyTicksRow.tsx" />
      </TableCell>
      <TableCell data-sentry-element="TableCell" data-sentry-source-file="MyTicksRow.tsx">
        <TickStyleBadge style={style} data-sentry-element="TickStyleBadge" data-sentry-source-file="MyTicksRow.tsx" />
      </TableCell>
      <TableCell sx={{
      textAlign: 'right'
    }} data-sentry-element="TableCell" data-sentry-source-file="MyTicksRow.tsx">
        {format(date, DEFAULT_DATA_FORMAT)}
      </TableCell>
      <TableCell data-sentry-element="TableCell" data-sentry-source-file="MyTicksRow.tsx">
        <TickMoreButton tick={fetchedTick.tick} data-sentry-element="TickMoreButton" data-sentry-source-file="MyTicksRow.tsx" />
      </TableCell>
    </TableRow>;
};