import { useState } from 'react';
import { RouteDistribution } from '../../FeaturePanel/Climbing/RouteDistribution';
import React from 'react';
import { FormControlLabel, Switch, Typography } from '@mui/material';
import { PanelSidePadding } from '../../utils/PanelHelpers';
import { OverpassFeature } from '../../../services/overpass/overpassSearch';
type MyTicksGraphsProps = {
  features: OverpassFeature[];
};
export const MyTicksGraphs = ({
  features
}: MyTicksGraphsProps) => {
  const [isGrouped, setIsGrouped] = useState(true);
  if (features.length === 0) {
    return null;
  }
  return <>
      <PanelSidePadding data-sentry-element="PanelSidePadding" data-sentry-source-file="MyTicksGraphs.tsx">
        <Typography variant="h6" mt={2} data-sentry-element="Typography" data-sentry-source-file="MyTicksGraphs.tsx">
          Routes distribution
        </Typography>
      </PanelSidePadding>
      <RouteDistribution features={features} cutEmptyMargins={!isGrouped} isGrouped={isGrouped} data-sentry-element="RouteDistribution" data-sentry-source-file="MyTicksGraphs.tsx" />
      <PanelSidePadding data-sentry-element="PanelSidePadding" data-sentry-source-file="MyTicksGraphs.tsx">
        <FormControlLabel control={<Switch checked={isGrouped} size="small" onChange={e => setIsGrouped(e.target.checked)} />} label="Grouping" data-sentry-element="FormControlLabel" data-sentry-source-file="MyTicksGraphs.tsx" />
      </PanelSidePadding>
    </>;
};